@import url(https://fonts.googleapis.com/css?family=Open + Sans);
@import url(https://fonts.googleapis.com/css2?family=Chango&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-large {
    padding: 9px 14px;
    font-size: 15px;
    line-height: normal;
    border-radius: 5px;
}

.btn-block {
    width: 100%;
    display: block;
}

* {
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    background: linear-gradient(90deg, #670d10, #092756);
}

.login {
    position: absolute;
    top: 40%;
    left: 50%;
    margin: -150px 0 0 -150px;
    width: 350px;
    height: 350px;
}
.login h1 {
    color: #fff;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    letter-spacing: 1px;
    text-align: center;
}

input {
    width: 100%;
    margin-bottom: 20px;
    margin-top: 5px;
    background: rgba(0, 0, 0, 0.3);
    border: none;
    outline: none;
    padding: 10px;
    font-size: 13px;
    color: #fff;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    box-shadow: inset 0 -5px 45px rgba(100, 100, 100, 0.2), 0 1px 1px rgba(255, 255, 255, 0.2);
    transition: box-shadow 0.5s ease;
}
input:focus {
    box-shadow: inset 0 -5px 45px rgba(100, 100, 100, 0.4), 0 1px 1px rgba(255, 255, 255, 0.2);
}

.createBtn {
  border-radius: 4px;
  background-color: rgb(0, 0, 0, .3);
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 20px;
  padding: 20px;
  width: 350px;
  transition: all 0.5s;
  cursor: pointer;
  margin-top: 10px;
}

.createBtn .createLink {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.createBtn .createLink:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.createBtn:hover .createLink {
  padding-right: 25px;
}

.createBtn:hover .createLink:after {
  opacity: 1;
  right: 0;
}

.createLink {
  text-decoration: none;
  color: rgb(226, 156, 168);
}

.labels {
  color: white;
}

.logoContainer {
  font-family: 'Chango', cursive;
  text-align: center;
  color: #c5cdda;
}

.movieicon {
  width: 70px;
  height: 70px;
}

.main-title {
  font-size: 2rem;
  margin: 0px;
}

.navbar {
    overflow: hidden;
    background-color: #333;
    position: fixed; 
    top: 0; 
    width: 100%;
  }
  
  .navbar a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 16px 32px;
    text-decoration: none;
    margin: 4px 2px;
    font-size: 16px;
  }

  .navbar a:hover {
    background: #ddd;
    color: black;
  }

  .btn {
    background-color: #333;
    border: 1px solid white;
    color: white;
    padding: 16px 32px;
    text-align: center;
    font-size: 16px;
    margin: 4px 2px;
    transition: 0.3s;
  }
  
  .btn:hover {
    background-color: #ddd;
    color: black;
  }
  
  .title-space {
    margin-top: 130px;
  }

  .welcome-comment {
    width: 50%;
    float: left;
    text-align: center;
    padding: 50px 30px;
    color: white;
    font-size: 1.3rem;
  }

  .select-start {
    width: 50%;
    float: left;
    text-align: center;
    justify-content: center;
    padding: 50px 30px;
  }

  .genreTxt {
    color: white;
    font-size: 1.2rem;
  }

  .box {
    margin-top: 20px;
  }
  
  .box select {
    background-color: #0563af;
    color: white;
    padding: 12px;
    width: 250px;
    border: none;
    font-size: 20px;
    box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
    -webkit-appearance: button;
    appearance: button;
    outline: none;
  }

  .btn-gradient-3d-orange {
    margin: 20px;
    font-size: 1.5rem;
    font-weight: 700;
    display: inline-block;
    padding: 0.5em 1em;
    text-decoration: none;
    border-radius: 4px;
    color: #ffffff;
    background-image: linear-gradient(45deg, #FFC107 0%, #ff8b5f 100%);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.29);
    border-bottom: solid 3px #c58668;
  }
  
  .btn-gradient-3d-orange:active {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
    box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);
    border-bottom: none;
  }

  .left-side {
    text-align: left;
    padding-left: 60px;
  }
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin-top: 59.6px;
  padding: 0;
}

.table {
  text-align: center;
  position: relative;
}

.table:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-top: 20px;
  font-size: 30px;
  font-weight: bold;
}

.overlay{
  position: absolute;
  left: 0; 
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(236, 235, 235, 0.8);
  z-index: 10;
  display: block;
}

.overlay-end{
  display: block;
}

button {
  margin: 0;
  padding: 0;
}

.card {
  margin: 3px;
  width: 250px;
  height: 285px;
  border: none;
}

.imgStyle {
  width: 250px;
  height: 285px;
}

.card:focus {
  outline: none;
  background: #ddd;
}

.card-front {
  background-image: url(https://drive.google.com/uc?export=view&id=1yTFnOjNL55k45ixbH4_YmlLkFFjtEs1k);
  background-size: 300px;
  cursor: pointer;
}

.card-front:hover {
  opacity: 0.7;
  transition: 1s;
}

.card-back {
  background-image: url(https://drive.google.com/uc?export=view&id=1yTFnOjNL55k45ixbH4_YmlLkFFjtEs1k);
  background-size: 300px;
  cursor: pointer;
}

.front {
  display: none;
}

.match {
  border: 3px solid red;
  border-radius: 4px;
}

.unmatch {
  border: 3px solid blue;
  border-radius: 4px;
}

.title {
  text-align: center;
  margin-top:238px;
  font-size: 3em;
  font-weight: bold;
  color:red;
}

.start-button {
  margin: 25px;
  font-size: 2.0em;
  font-weight: bold;
  padding: 2px 14px;
  color: #fff;
  border-style: none;
  box-shadow: 2px 2px 3px 1px #666;
  -moz-box-shadow: 2px 2px 3px 1px #666;
  -webkit-box-shadow: 2px 2px 3px 1px #666;
  text-shadow: 1px 1px 2px #000;
  background: -moz-linear-gradient(bottom, #36d, #248 50%, #36d);
  background: -webkit-gradient(linear, left bottom, left top, from(#36d), color-stop(0.5, #248), to(#36d));
  border-radius: 5px;
  cursor:pointer;
}

.start-button:hover{
  opacity:0.7;
}

.count-number {
  margin-left: 20px;
  font-size: 2.0em;
  font-weight: bold;
}

.status-container {
  display: flex;
}

.count-number {
  margin: 25px;
  color: white;
}

.gameStatus {
  margin: 25px;
  width: auto;
  font-size: 2rem;
  color: white;
  text-align: center;
  text-align: justify;
}

.scoreContainer {
  position: absolute;
  top: 50%;
  right: 0px;
  bottom: 0px;
  left: 0px;
  margin: auto;
  width: 400px;
  height: 300px;
  text-align: center;
  align-items: center;
  border-radius: 20px;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: white;
  z-index: 11;
}

.scoreTitle {
  text-align: center;
  width: 400px;
  height: 50px;
  margin-top: 5px;
  margin-bottom: 20px;
  font-size: 2.5rem;
  border-radius: 20px 20px 0 0;
}

.score {
  display: flex;
  width: 350px;
  height: 40px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
}

.scoreDetail {
  font-size: 1.2rem;
  width: 50%;
  margin-top: auto;
  margin-bottom: auto;
}

.scoreNumber {
  font-size: 1.2rem;
  width: 50%;
  margin-top: auto;
  margin-bottom: auto;
}

#timeColor {
  border-left: 10px solid #fa7f7f;
}

#attemptColor {
  border-left: 10px solid #f8fba2;
}

#accuracyColor {
  border-left: 10px solid #a0e4b0;
}

hr {
  width: 300px;
}

.playAgain {
  background-color: #d5f4e6;
  border: none;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 30px 4px 0;
  cursor: pointer;
  border-radius: 16px;
}

.playAgain:hover {
  background-color: #80ced6;
}

.scores {
  background-color: #f7cac9;
  border: none;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 0 4px 30px;
  cursor: pointer;
  border-radius: 16px;
}

.scores:hover {
  background-color: #ff7b25;
}

.scoreLink {
  text-decoration: none;
  color: black;
}
.recordTable{
    width: 100%;
    border-collapse: collapse;
    border-right: 1px solid #8fd2ed;
    text-align: center;
}
.recordTable th{
    padding: 10px;
    background-color: #cee7ff;
    border-left: 5px solid #8fd2ed; 
    border-top: 1px solid #fff;
}
.recordTable td{
    padding: 10px;
    background-color: #fff;
    border: 1px solid #999;
}

.gold {
    background-color: #e9e66e;
    font-size: 1.2rem;
}

.silver {
    background-color: #e2e2e2;
    font-size: 1.2rem;
}

.bronze {
    background-color: #aa8b15;
    font-size: 1.2rem;
}

